@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  --primary: #282838;
  --secondary: #ff6a82;
}

span {
  color: var(--secondary);
  margin: 10px, 0px;
  font-family: "Fira Sans";
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100%;
  width: 100%;
}

.home {
  background-color: var(--primary);
  height: 100%;
}

.home__body {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.home__text {
  color: white;
  padding: 10%;
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(to bottom, #12131e, var(--primary));
}
.home__text > h1 {
  font-size: 3em;
}
.home__text > h4 {
  font-weight: 100;
  font-size: 1.3em;
}

@media screen and (max-width: 500px) {
  .home__text {
    background-color: var(--primary);
    background-image: none;
  }
  .home__text {
    padding: 5%;
    font-size: 0.8em;
    margin-top: 10%;
  }
  .home__text > h1 {
    padding-bottom: 20px;
  }
  .home__text > h4 {
    line-height: 1.5;
  }
}

.header {
  background-color: #12131e;
  color: white;
}
.header__body {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
.header__logo {
  display: flex;
}
.header__logo > * {
  font-weight: 500;
  font-size: 3em;
}
.header__logo > span > .MuiSvgIcon-root {
  font-size: 1.2em !important;
  margin-right: 20px;
}

.header__right {
  margin-top: auto;
  margin-bottom: auto;
  margin-top: -10px;
}

.MuiSvgIcon-root {
  color: white !important;
}

@media screen and (max-width: 420px) {
  .MuiSvgIcon-root {
    font-size: 1.2em !important;
  }
  .header__left {
    margin: auto;
  }
  .header__logo > * {
    font-size: 1.5em;
    margin: 0;
  }
  .header__right {
    display: flex;
  }
}

.gamecards {
  background-color: var(--primary);
  display: flex;
  justify-content: space-evenly;
  overflow: hidden;
  flex-wrap: wrap;
  min-height: 80vh;
}

@media screen and (max-width: 500px) {
  .gamecards {
    display: inline;
    display: initial;
    justify-content: initial;
    overflow: visible;
    overflow: initial;
  }
}

.card {
  transition: all 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
  width: calc(55vh * 0.625);
  height:55vh;
  padding: 50px;
}

.card:hover {
  transform: scale(1.1);
}

.card__body {
  box-shadow: 1px 1px 20px 1px rgb(88, 88, 88);
  height: 100%;
  width: 100%;
}

.card__title > * {
  color: white;
  font-weight: 400;
  text-align: center;
  font-size: x-large;
}

.card__image{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.advertbuild {
  width: 95%;
  margin: auto;
}

.advertbuild__title {
  text-align: center;
  font-size: 2em;
  padding: 5%;
}

.advertbuild__title > * > h1 {
  margin: 20px;
}

.advertbuild__title > * > h4 {
  font-weight: 100;
  font-size: 0.8em;
}
@media screen and (max-width: 500px) {
  .advertbuild__title {
    text-align: left;
  }
  .advertbuild__title > * > h1 {
    margin: 0;
    font-size: 1.4em;
  }
  .advertbuild__title > * > h4 {
    font-weight: 100;
    font-size: 0.7em;
    line-break: 1.5;
  }
}

.buildsummary {
  display: flex;
  background-color: white;
  border-radius: 10px;
  margin: 40px;
  min-height: 250px;
}

.buildsummary__details {
  display: flex;
}

.buildsummary__loading > * {
  margin: auto;
}

.buildsummary__image {
  display: flex;
  justify-content: space-between;
  height: 300px;
  width: 400px;
  margin: auto;
}

.buildsummary__case {
  object-fit: contain;
  height: 50%;
  margin: auto;
}

.buildsummary__partsImg {
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 90%;
}

.buildsummary__partsImg > img {
  height: 50%;
  object-fit: contain;
  padding: 10px;
}

.buildsummary__text {
  width: 20%;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #212121;
  color: white;
}
.buildsummary__loadBars {
  width: 20%;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgb(255, 136, 0);
  color: white;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 40px;
}

@media screen and (max-width: 900px) {
  .buildsummary__image {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .buildsummary {
    flex-direction: column;
    margin: 5%;
  }
  .buildsummary__image {
    display: none;
  }
  .buildsummary__details {
    flex-direction: column;
    width: 100%;
  }
  .buildsummary__text {
    padding: 20px;
    height: 50%;
    width: auto;
    width: initial;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
  }
  .buildsummary__text > * {
    margin: 5px;
  }
  .buildsummary__loadBars > * {
    height: 50%;
  }
  .buildsummary__loadBars {
    border-radius: 0;
    width: auto;
    width: initial;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}

.build {
  background-color: #282838;
}
.build__body {
  height: 100%;
  width: 100%;
}

.build__cards {
  margin: auto;
  width: 90%;
  height: 70%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.build__featured > h1 {
  font-size: 4em;
  font-family: "Fira Sans", sans-serif;
  font-weight: 400;
  color: white;
  text-align: center;
  padding: 40px;
}

@media screen and (max-width: 500px) {
  .build__cards {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
  }
  .build__featured > h1 {
    font-size: 3em;
    font-family: "Fira Sans", sans-serif;
    font-weight: 400;
    color: white;
    text-align: center;
    padding: 20px;
    margin-top: 20px;
  }
}

.buildcard {
  background-color: white;
  border-radius: 5px;
  border: 5px solid;
  margin: 40px;
  padding: 20px;
  min-width: 400px;
  min-height: 400px;
}

.buildcard:hover {
  cursor: pointer;
}

.buildcard__image {
  display: flex;
  justify-content: space-between;
  height: 300px;
  width: 400px;
}

.buildcard__case {
  object-fit: contain;
  height: 40%;
  margin: auto;
}

.buildcard__partsImg {
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 70%;
}

.buildcard__partsImg > img {
  height: 50%;
  object-fit: contain;
  padding: 10px;
}

.buildcard__loading {
  display: flex;
}

.buildcard__loading > * {
  margin: auto;
}

@media screen and (max-width: 500px) {
  .buildcard {
    margin: 20px;
    min-width: 0;
    min-width: initial;
    min-height: 0;
    min-height: initial;
  }
  .buildcard__image {
    width: 40%;
  }
}

.buildelab {
  margin: 20px;
  color: white;
  padding: 40px;
}

.buildelab__scores {
  font-size: 1em;
}

.buildelab__titles {
  font-size: 1em;
}

.buildelab__titles {
  border-bottom: 1px solid white;
  margin-bottom: 30px;
}

.buildelab__titles > * {
  margin-bottom: 30px;
}

@media screen and (max-width: 500px) {
  .buildelab {
    margin: 0;
    padding: 20px;
  }
  .buildelab__titles {
    font-size: 0.8em;
    margin: 0;
    line-height: 20px;
  }
  .buildelab__titles > * {
    margin-bottom: 20px;
  }
}

.part {
  color: white;
  margin-bottom: 10%;
  min-height: 20%;
}
.part__title {
  display: flex;
  margin: 10px;
  margin-left: 0;
  font-size: x-large;
}
.part__line {
  border: 1px solid blue;
  background-color: blue;
  width: 100%;
  margin: 17px;
}

.part__body {
  display: flex;
  justify-content: space-around;
  height: 10%;
}
.part__body > h3 {
  margin: auto;
  font-weight: 600;
  margin-left: 10%;
  font-size: large;
}

.part__image {
  width: 10%;
  object-fit: contain;
}

.part__details {
  display: flex;
  justify-content: space-between;
  padding: 30px;
  padding-left: 0;
  position: relative;
  left: 0;
}

.part__buyBtn {
  background-color: #3b3b53 !important;
}

.part__loading {
  display: flex;
}
.part__loading > * {
  margin: auto;
  color: aliceblue;
}

@media screen and (max-width: 500px) {
  .part__image {
    width: 20%;
    object-fit: contain;
  }
  .part__body {
    justify-content: space-between;
    margin: 0;
  }
  .part__body > h3 {
    margin: auto;
  }
  .part__details {
    flex-wrap: wrap;
    margin-top: 20px;
    padding: 0;
  }
  .part__details * {
    margin: 10px;
  }
  .part__buyBtn {
    background-color: #3b3b53 !important;
    width: 100% !important;
    margin: 10px !important;
  }
}

